.svg-wrapper {

  /* float: left; */
  margin: 0;
  padding: 0;
  /* text-align: left; */
  width: 100%;
}

.svg-wrapper svg {
  display: block;
  max-width: calc(100vw - (100vw / 2.2));
  max-height: calc(100vw - (100vw / 3));
  margin-right: auto;
  margin-left: auto;
  width: 100%;
}

.map-border {
  fill: #242E47;
  fill: #f2f2f2
}

.map-province {
  fill: #364669;
  fill: #076ee496;
  transition: fill 0.3s ease;
}

.map-province:hover {
  fill: #CA466B;
  fill: #f34254a6;
}

.map-province.selected {
  fill: #CA466B;
  fill: #f34254;
  fill: #ff6384;
}

.map-sea {
  fill: #158690;
  fill: #0063d1;
}

.map-island {
  fill: #364669;
}

.map-sea-names {
  fill: #D4DBE5;
}

.map-province-names {
  fill: #D4DBE5;
  fill: #fff;
}