.indicator {
    /* background-color: #000 !important; */
    /*background-image: url("../md-pro/assets/Img/indicator.gif");*/
    /* background-position: center center;
    background-repeat: no-repeat; */
    position: absolute;
    z-index: 99999999;
    /* opacity: 0.2; */
    /* -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)"; */
    /* filter: alpha(opacity=80); */
    /* z-index: 1000; */
}

.indicator::after {
    content: "";
    background-color: rgba(0, 99, 209, 0.15) !important;
    /* opacity: 0.1 !important; */
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
}

.load {
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 1;
    transform: translate(-50%, -50%);
    /*change these sizes to fit into your project*/
    width: 70px;
    height: 70px;
}


.indicator .load {
    top: calc(50% - 40px);
    left: calc(50% - 40px);
}

.load hr {
    border: 0;
    margin: 0;
    width: 40%;
    height: 40%;
    position: absolute;
    border-radius: 50%;
    animation: spin 2s ease infinite;
}

.load :first-child {
    background: #19A68C;
    animation-delay: -1.5s;
}

.load :nth-child(2) {
    background: #F63D3A;
    animation-delay: -1s;
}

.load :nth-child(3) {
    background: #FDA543;
    animation-delay: -0.5s;
}

.load :last-child {
    background: #193B48;
}

@keyframes spin {

    0%,
    100% {
        transform: translate(0);
    }

    25% {
        transform: translate(160%);
    }

    50% {
        transform: translate(160%, 160%);
    }

    75% {
        transform: translate(0, 160%);
    }
}


.spin-wrapper {
    position: relative;
    /* width: 100%;
    height: 100vh; */
    /* background: #080705; */
    position: absolute;
    top: calc(50% - 25px);
    left: calc(50% - 25px);
    opacity: 1;
    transform: translate(-50%, -50%);
}

.spin-wrapper .spinner {
    position: absolute;
    height: 100px;
    width: 100px;
    border: 4px solid transparent;
    border-top-color: #E46E26;
    top: 50%;
    left: 50%;
    margin: -30px;
    border-radius: 50%;
    animation: spin 2s linear infinite;
}

.spin-wrapper .spinner:before,
.spin-wrapper .spinner:after {
    content: '';
    position: absolute;
    border: 4px solid transparent;
    border-radius: 50%;
}

.spin-wrapper .spinner:before {
    border-top-color: #6F0C44;
    top: -12px;
    left: -12px;
    right: -12px;
    bottom: -12px;
    animation: spin 3s linear infinite;
}

.spin-wrapper .spinner:after {
    border-top-color: #FFFBFE;
    top: 6px;
    left: 6px;
    right: 6px;
    bottom: 6px;
    animation: spin 4s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}



$animation-timing: 2s;
$dot-color: #f34255;
$dot-size: 18px;
$dot-offset: -$dot-size / 1.5;
$dot-opacity: 0.5;

.loadingDots{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    strong{
        margin-top: 20px;
    }
}

.dots {
	text-align: center;
	font-size: $dot-size;
	//   padding: $dot-size
	//  * 5;
	padding-top: 10px;

	.dot {
		width: 1em;
		height: 1em;
		margin: 0 .3em;
		display: inline-block;
		background: $dot-color;
		border-radius: 50%;
		position: relative;

		&:nth-child(1) {
			animation: ease-in-out dot1 $animation-timing infinite;
		}

		&:nth-child(2) {
			animation: ease-in-out dot2 $animation-timing infinite;
		}

		&:nth-child(3) {
			animation: ease-in-out dot3 $animation-timing infinite;
		}

		&:nth-child(4) {
			animation: ease-in-out dot4 $animation-timing infinite;
		}

		&:nth-child(5) {
			animation: ease-in-out dot5 $animation-timing infinite;
		}

		&:nth-child(6) {
			animation: ease-in-out dot6 $animation-timing infinite;
		}

	}

}


@keyframes dot1 {
	0% {
		top: 0;
	}

	10% {
		top: $dot-offset;
		opacity: $dot-opacity;
	}

	20% {
		top: 0;
	}
}

@keyframes dot2 {
	10% {
		top: 0px
	}

	20% {
		top: $dot-offset;
		opacity: $dot-opacity;
	}

	40% {
		top: 0px
	}
}

@keyframes dot3 {
	20% {
		top: 0px
	}

	40% {
		top: $dot-offset;
		opacity: $dot-opacity;
	}

	60% {
		top: 0px
	}
}

@keyframes dot4 {
	40% {
		top: 0px
	}

	60% {
		top: $dot-offset;
		opacity: $dot-opacity;
	}

	80% {
		top: 0px
	}
}

@keyframes dot5 {
	60% {
		top: 0px
	}

	80% {
		top: $dot-offset;
		opacity: $dot-opacity;
	}

	100% {
		top: 0px
	}
}
