.login button {
  background: $color2 !important;
  color: #fff;
  border: none !important;
  border-radius: 25px;
  padding: 10px 80px;
  font-weight: bold;
  font-size: 14px;
  display: block;
  width: 100%;
  margin: 30px 0;
  height: 50px;
}

.login .text-input {
  background-color: #fff;
  padding: 8px 12px;
  border-radius: 25px;
  box-shadow: none;
  border: 1px solid $color2 !important;
  width: 100%;
  font-family: "IRANSans", arial;
  height: 50px;
  text-align: center;
}

.login .text-input::placeholder {
  font-size: 12px;
  direction: rtl;
  text-indent: unset;
  color: #A1A1A1;

}

.login input:-internal-autofill-selected {
  background-color: $color2 !important;
}

.login .text-input.has-error {
  border: thin solid red !important;

}

.login-image {
  padding: 20px;
  margin: auto;
  margin-bottom: 50px;
  width: 130px;
  height: 130px;
  border-radius: 50%;
  background: $color2;
  overflow: hidden;
  opacity: 1;
  box-shadow: 0px 0px 15px #9a908a;
}

.login-image img {
  width: 100%;
}

.login .header h1 {
  font-size: 22px;
  font-weight: bold;
  text-align: center;
}

.login h1 {
  letter-spacing: -1px;
}

.login .header p {
  font-weight: bold;
  font-size: 15px;
  text-align: center;
  line-height: 1.8em;
}

.login .header {
  margin-top: 50px;
}

