/* ===================== Document Uploader loader ===================== */
#wrapperr {
  width: 90px;
  margin: 32px auto;
  position: relative;
  background: none;
}

#wrapperr #loader {
  position: relative;
  width: 100%;
  height: 6px;
  border: 1px solid #F34255;
  border-radius: 10px;
  -webkit-animation: turn 4s linear 1.75s infinite;
          animation: turn 4s linear 1.75s infinite;
  background: none;
}

#wrapperr #loader:before {
  content: '';
  display: block;
  position: absolute;
  width: 0%;
  height: 100%;
  background: #F34255;
  box-shadow: 10px 0px 15px 0px #F34255;
  -webkit-animation: load 2s linear infinite;
          animation: load 2s linear infinite;
}

#wrapperr #loadText {
  width: 100%;
  position: absolute;
  top: 10px;
  color: #F34255;
  font-size: 1.4em;
  text-align: center;
  -webkit-animation: bounce 2s linear infinite;
          animation: bounce 2s linear infinite;
}

@-webkit-keyframes load {
  0% {
    width: 0%;
  }

  87.5%,
  100% {
    width: 100%;
  }
}

@keyframes load {
  0% {
    width: 0%;
  }

  87.5%,
  100% {
    width: 100%;
  }
}

@-webkit-keyframes turn {
  0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }

  6.25%,
  50% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }

  56.25%,
  100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}

@keyframes turn {
  0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }

  6.25%,
  50% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }

  56.25%,
  100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}

@-webkit-keyframes bounce {

  0%,
  100% {
    top: 10px;
  }

  12.5% {
    top: 30px;
  }
}

@keyframes bounce {

  0%,
  100% {
    top: 10px;
  }

  12.5% {
    top: 30px;
  }
}

