
/*
 Larger screen styling
 Horizontal menu
*/
/* @media (min-width: 768px) { */

  .menu-toggle,
  .main-menu .menu-close,
  #main-menu-checkbox {
    display: none;
  }

  /* Undo positioning of off-canvas menu */
  .main-menu {
    position: relative;
    right: auto;
    top: auto;
    height: auto;
    /* overflow-y: auto; fix dumb MS Edge scrollbar issue */
    height: 50px;
    /* overflow-y: hidden; */
  }

  .main-menu ul {
    display: block;

    /* Undo off-canvas styling */
    padding: 0;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    height: auto;
    width: auto;
    background: none;
    margin: 0
  }

  .main-menu>ul>li>a {
    border-left: thin solid #dcdcdc !important;
    padding: 0 10px;
    margin: 10px 0;
  }

  .main-menu>ul>li>a:hover {
    background: unset;
  }

  .main-menu>ul>li:last-child>a {
    border-left: none !important
  }

  .main-menu>ul>li>a {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 12px;
    padding: 0px 5px;
  }

  .main-menu ul li a span {
    flex: 2;
  }

  .main-menu ul li a svg:first-child {
    color: #0063d1;
    align-self: flex-start;
    margin-left: 5px;
  }

  .main-menu ul li a svg:last-child {
    color: #000;
    align-self: flex-end;
    font-size: 20px;
  }

  .main-menu>ul>li {
    background: transparent !important;
    position: relative;
    display: block;
    float: right;
    min-width: 150px;
    cursor: pointer;
  }
  li.nav-item.active, .main-menu>ul>li.active>a, .sidebar .nav li:hover>a, .sidebar .nav li.active>[data-toggle="collapse"]{
    color: #fff;
    background: transparent !important;
  }
  .main-menu>ul>li.active>a>span{
    color: #000;
  }
  .main-menu>ul>li>div {
    background: #fff;
    display: none !important;
    position: absolute;
    top: 44px;
    right: 0;
    width: 100%;
    z-index: 999;

    -webkit-border-bottom-right-radius: 5px;
    -webkit-border-bottom-left-radius: 5px;
    -moz-border-radius-bottomright: 5px;
    -moz-border-radius-bottomleft: 5px;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    overflow: hidden;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 9px 1px, rgba(0, 0, 0, 0.1) 0px 10px 5px -15px;
  }

  .main-menu>ul>li>div>ul {
    display: flex;
    flex-direction: column;
  }

  .main-menu>ul>li:hover>div {
    display: block !important;
    
  }

  .main-menu a {
    color: #000;
    border: 0 !important;
    /* Remove borders from off-canvas styling */
    font-size: 12px;
  }

  .main-menu>ul>li>div>ul>li>a {
    padding: 10px;
    text-indent: 0 !important;
  }

  .main-menu a:hover,
  .main-menu a:focus {
    background: none;
    /* Remove background from off-canvas styling */
    color: #000;
  }
/* } */

/*
 Demo purposes only
