*,
*:before,
*:after {
  box-sizing: border-box;
}

header {
  padding: 0px 20px;
  display: flex;
  align-items: baseline;
  background: #fff !important;
}

header nav {
  overflow-x: hidden !important;
}

.root-li {
  border: none;
}

.logo-bar {
  height: 70px;
  background: #0063d1;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 9px 1px, rgba(0, 0, 0, 0.1) 0px -5px 2px 2px;
}

.avatar {
  z-index: 101;
  width: 75px !important;
  height: 75px !important;
  border-radius: 50%;
  border: 2px solid #f0f0f0;
  /* margin-left: 10px; */
  position: absolute;
  right: -40px;
  top: 10px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 9px 1px, rgba(0, 0, 0, 0.1) 0px 0px 10px 5px;
}
.avatar-sm {
  width: 50px !important;
  height: 50px !important;
  border-radius: 50%;
  border: 2px solid #f0f0f0;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 9px 1px, rgba(0, 0, 0, 0.1) 0px 0px 10px 5px;
}

.tab-content {
  position: relative !important;
}
.user-fullname{
  color: #fff;
  margin: 20px 35px 0 0;
  font-size: 14px;
  font-weight: bold;
}
.user-fullname span{
  color: #dedcdc;
  font-size: 11px;
  font-weight: normal;
}

ul > li i:first-child{
  margin-left: 10px;
}

.header{
  background: #0063d1;
  height: 70px;
  box-shadow: rgba(111, 12, 68, .4) 0px -2px 5px 4px
}
.top-navbar{
  min-height: 50px;
  padding: .5em 0 0;
  box-shadow: 0 5px 5px -5px rgba(0,0,0,.1);
}
.top-navbar .row{
  background: #fff;
}

.main-wrapper{
  padding: 30px ;
  min-height: calc(100vh - 127px);
}

.mobile-menu ul > li > a {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.mobile-menu ul > li > div > ul > li > a{
  /* text-indent: 55px !important; */
}
.mobile-menu ul > li i:last-child{
  margin-left: 0 !important;
}
.mobile-menu ul > li i:first-child{
  margin-right: 15px !important;
  margin-left: 10px !important;
}
.mobile-menu ul li:hover, .mobile-menu ul li.active, .mobile-menu ul li:hover i:first-child, .mobile-menu ul li.active i:first-child, div.collapse.in ul.nav li.active a {
  background: #0063d1 !important;
  color: #fff !important;
}
.mobile-menu ul > li > div > ul > li:hover, .mobile-menu ul > li > div > ul > li:hover > a{
  background: #0063d1 !important;
  color: #fff !important;
}
.mobile-menu ul > li:hover>span {
  background: #0063d1;
  color: #fff !important;
}

.mobile-menu ul > li > a > span{
  flex: 2;
  text-align: right;
}

.sidebar:before, .sidebar:after{
  content: none !important;
}

.sidebar .nav, .ui.visible.push.sidebar{
  background: #fff !important;
}

.main-menu > ul > li > div {
  overflow: visible;
}
.main-menu > ul > li > div > ul li div {
  display: none !important;
}
.main-menu > ul > li > div > ul li:hover  div {
  display: block !important;
}

.main-menu ul.nav li:hover, .main-menu ul.nav li a:hover{
  background: #eee !important;
}

.main-menu > ul > li > div > ul  li  div{
  position: absolute;
  right: 100%;
  top: 0;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) -2px 0px 5px 1px;
  width: 200px;
}
.main-menu > ul > li > div > ul  li  div ul{
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) -0px 0px 5px 1px;
}

.main-menu>ul>li> div ul li a {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 12px;
  padding: 0px 5px;
  padding: 10px 5px;
  text-indent: 25px;
    direction: rtl;
}
.main-menu>ul>li> div ul li a span {
  flex: 2 1;
}
.main-menu>ul>li> div ul li a i:last-child {
  color: #000;
  align-self: flex-end;
  font-size: 20px;
}
.main-menu>ul>li> div ul li a i:last-child::before {
  content: "\F141"
}
.main-menu>ul>li> div ul li a i:first-child {
  color: #0063d1;
  align-self: flex-start;
}

.tab-content{
  z-index: 10;
}

.mobile-menu div ul.nav li a {
  text-indent: 25px
}

.sidebar .nav li>a{
  line-height: 30px;
}

.sidebar .nav li>a svg:first-child {
