}
.sidebar .nav li>a svg:last-child {
  color: #000;
  align-self: flex-end;
  font-size: 20px;
}

.sidebar .nav li{
  border-top: thin solid #eee !important;
}
.sidebar .nav li:last-child{
  /* border-top: none !important; */
}

.nav.nav-tabs .nav-item.active {
  background: #0063d1;
  color: #fff;
}

.indicator{
  /* background-color: rgba(238, 238, 238, .7); */
