}



.required_validator {
  top: 10px;
  left: 15px;
  position: absolute;
  display: block;
  opacity: 0;
  /* width: 320px; */
  height: auto;
  -webkit-box-sizing: border-box;
  -khtml-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  opacity: 1;
  /* z-index: 99; */
}

.MuiDialogContent-root .form-inline .required_validator {
  top: -5px;
}

.validator-table .required_validator {
  top: -7px;
}

.validator-table .required_validator .validator_container {
  border-radius: 25px;
  background-color: #f50057;
  padding: 2px 10px;
}

.validator-table td {
  /* width: 200px;
  max-width: 200px; */
  position: relative;
}

.tab-pane .form-inline .required_validator {
  top: 10px;
}

.required_validator:after {
  display: block;
  position: absolute;
  right: 49%;
  bottom: -4px;
  content: '';
  margin-right: -5px;
  border-top: 5px solid #f50057;
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
}

.required_validator .validator_container {
  display: block;
  position: relative;
  -webkit-box-sizing: border-box;
  -khtml-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  background-color: #f50057;
  color: #fff;
  font-size: 11px;
  padding: 0 3px;
  white-space: nowrap;
}


.required_validator.visible {
  -o-transform: translateX(0px);
  -webkit-transform: translateX(0px);
  -moz-transform: translateX(0px);
  -ms-transform: translateX(0px);
  transform: translateX(0px);
  opacity: 1;
}

.required_validator.invisible {
  -o-transform: translateX(-15px);
  -webkit-transform: translateX(-15px);
  -moz-transform: translateX(-15px);
  -ms-transform: translateX(-15px);
  transform: translateX(-15px);
  opacity: 0;
  -webkit-animation-delay: 2s;
  /* Chrome, Safari, Opera */
  animation-delay: 2s;
}


















