  /* height: calc(100vh - 250px); */
  /* max-height: calc(100vh / 2); */
  height: 100%;
  width: 100%;
  max-width: 80%;
}

.box {
  background: #fff;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 3px 15px #ccc;
  min-height: 350px;
  width: 100%;
}

.timeline-data {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
}

.has-gift .vertical-timeline-element-content {
  border-color: #91d0ba !important;
}

.has-gift .timeline-data,
.has-gift .vertical-timeline-element-content-arrow {
  background-color: #37dca369;
  border-color: #91d0ba;
}

.timeline-infos {
  direction: rtl;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
}


.timeline-item {
  display: flex;
  flex-direction: column;
  /* box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.15); */
  margin: 5px 0;
  /* background: #fff; */
  /* border-radius: 10px; */
  border-bottom: 1px solid #29ce963d;
}


.timeline-item .key {
  border-bottom: 1px solid #f7f7f7;
  padding: 10px 5px;
}
.timeline-item .value {
  padding: 10px 5px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}
.timeline-item .value svg{
  vertical-align: middle;
  margin-right: 5px;
}